@import url('https://fonts.googleapis.com/css?family=Roboto:300,400');
 body{
     margin: 0px;
     padding: 0px;
     background-color: #ffffff;
     color: #1c1c2d;
     width: 100%;
     font-family: 'Roboto', sans-serif;
     -webkit-user-select: none;
     -moz-user-select: none;
     -ms-user-select: none;
     user-select: none;
}
 .main-container{
     width: 100%;
}
 header{
     display: block;
     background-color: #1c1c2d;
     width: 100%;
     padding: 20px 10px;
     position: fixed;
     top:0px;
     left: 0px;
     z-index: 3;
}
 header .container{
     display: block;
     width: 100%;
     max-width: 1044px;
     margin: 0 auto;

}
 .logo{
     float: left;
     display: inline-block;
     width: 92px;
     height: 45px;
}
 nav{
     width: 300px;
     background-color: #1c1c2d;
     color: #fff;
     float: right;
     padding: 5px 0 0 0;
}
 .popular,  .favorites{
     font-size: 16px;
     display: inline-block;
     padding: 8px;
     
}

 a{
     text-decoration: none;
     color: #fff;
 }
 .search {
     position: relative;
     display: inline-block;
     padding: 8px;
     color: #fff;
     font-size: 16px;
}
 .search:before {
     content: "\F002";
     font-family: FontAwesome;
     font-style: normal;
     font-weight: normal;
     text-decoration: inherit;
     font-size: 18px;
     padding: 2px 2px 0px 8px;
     position: absolute;
     top: 0px;
     left: 0;
     z-index: 2;
}
 .search:hover{
     color: #1c1c2d;
}
 .search:hover input, .search:active input, .search:checked input, .search:focus input{
     display: block;
     width: 215px;
     left: -200px;
     top: -5px;
     padding: 8px;
     background-color: #fff;
}
 .search input{
     position: absolute;
     top: 0px;
     left: 0px;
     z-index: 1;
     width: 0px;
     font-size: 12px;
     transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
     border-radius: 10px;
     border: 0px;
     outline: 0px;
     background-color: #1c1c2d;
}
 .search-input input:focus{
     outline: 0px;
     border: 0px;
}
 .clearfix{
     clear: both;
}

input:focus + .search_results{
     display: block;
     visibility: visible;
}
.search_results{
     position: absolute;
     display: none;
     width: 220px;
     top:25px;
     left:-200px;
     padding: 5px;
     font-size: 10px;
     background-color: #fff;
     color: #1c1c2d;
     visibility: hidden;
     z-index: 999;
     border-radius: 10px;
     border:1px solid #1c1c2d;

}
 .filter{
     position: relative;
     top: 85px;
     max-width: 1064px;
     width: 100%;
     margin: 0 auto;
}
 .sort{
     width: 140px;
     font-size: 12px;
     display: inline-block;
     position: absolute;
     top:30px;
     left:20px;
}
 .sort .container{
     border-bottom: 2px solid #333;
     padding: 5px 0;
}
 .sort_text{
     display: inline-block;
     float: left;
     padding: 0px 5px;
}
 .sort_icon{
     display: inline-block;
     float: right;
     padding: 0px 5px;
}
 .sort .list{
     width: 100%;
     background-color: rgba(0, 0, 0, 0.4);
     opacity: 0;
     visibility: hidden;
     height: 0px;
     color: #ffffff;
     font-size: 16px;
     position: relative;
     z-index: 2;
     text-align: left;
     padding: 0px;
     top:0px;
     font-weight: 300;
     margin: 0px;
     list-style-type:none;
     transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
}
 .sort .items{
     padding: 5px 13px;
     cursor: pointer;
     background:#000000;
     opacity: 0.7;
     transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
}
 .sort .items:hover{
     opacity: 1;
}
 .sort:hover .list{
     height: auto;
     visibility: visible;
     opacity: 1;
}
 .sort:hover .list .items{
     padding: 7px 13px;
}
 .year{
     width: 60px;
     font-size: 12px;
     position: absolute;
     top: 30px;
     left:181px;
     display: inline-block;
}
 .year .container{
     border-bottom: 2px solid #333;
     padding: 5px 0;
}
 .year_text{
     display: inline-block;
     float: left;
     padding: 0px 5px;
}
 .year_icon{
     display: inline-block;
     float: right;
     padding: 0px 5px;
}
 .year .list{
     width: 100%;
     background-color: rgba(0, 0, 0, 0.4);
     opacity: 0;
     visibility: hidden;
     height: 0px;
     color: #ffffff;
     font-size: 16px;
     position: relative;
     z-index: 2;
     padding: 0px;
     top:-5px;
     font-weight: 300;
     text-align: left;
     margin: 0px;
     list-style-type:none;
     transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
}
 .year .items{
     padding: 5px 13px;
     cursor: pointer;
     background:#000000;
     opacity: 0.7;
     transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
}
 .year .items:hover{
     opacity: 1;
}
 .year:hover .list{
     height: auto;
     visibility: visible;
     opacity: 1;
}
 .year:hover .list .items{
     padding: 7px 13px;
}
 .movie-card-container{
     display: block;
     position: relative;
     top: 175px;
     max-width: 1044px;
     width: 100%;
     margin: 0 auto;
}
 .movie-card-box{
     display: inline-block;
     padding: 10px;
     transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
     vertical-align: top;
}
 .movie-card{
     width: 301px;
     height: 345px;
     position: relative;
     top: 0px;
     left:0px;
     background-repeat: no-repeat;
     background-position: center center;
     background-size: 100% 100%, cover;
     box-shadow: 0 0 40px rgba(0,0,0,0.6);
     -moz-box-shadow: 0 0 40px rgba(0,0,0,0.6);
     -webkit-box-shadow: 0 0 40px rgba(0,0,0,0.6);
     -o-box-shadow: 0 0 40px rgba(0,0,0,0.6);
     transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
}
 .movie-overlay{
     width:100%;
     height:100%;
     background:rgba(0,0,0,0.4);
}
 .movie-card-box:hover{
     padding: 0px;
}
 .movie-card-box:hover .movie-card{
     width: 321px;
     height: 365px;
     top: -5px;
     left:-5px;
}
 .movie-card:hover .movie-overlay{
     background:rgba(0,0,0,0);
}
 .movie-card-wrapper{
     padding: 6px;
     color: #fff;
     height: 323px;
}
 .top-section{
     font-size: 12px;
     font-weight: 300;
     vertical-align: top;
}
 .date-section{
     padding: 5px;
     display: inline-block;
}
 .rating-section{
     display: inline-block;
     float: right;
}
 .heart, .comment, .star{
     font-family: FontAwesome;
     font-size: 12px;
     padding: 5px;
     display: inline-block;
}
 .votes{
     font-size: 12px;
     font-family: 'Roboto', sans-serif;
     font-weight: 300;
}
 .fas,.far{
     font-style: normal;
}
.bottom-section{
     position: absolute;
  bottom: -30px;
  transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
}
 .language-section{
     display: block;
     padding: 5px;
     font-size: 14px;
     text-transform: uppercase;
     font-weight: 300;
}
 .title-section{
     display: block;
     padding: 5px;
     font-size: 20px;
     text-transform: uppercase;
     font-weight: 300;
     line-height: 26px;
}
 .social-icons{
     opacity:0;
     padding: 0 0 5px 0;
     visibility: hidden;
}
 .icons{
     display: inline-block;
     font-size: 14px;
     position:relative;
     left: 200px;
     text-align:left;
     width:0px;
     height:0px;
     padding:15px;
     border-radius: 20px;
     transition: all 0.4s ease;
     -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
}
 .icons i {
     position:absolute;
     left:9px;
     top:9px;
}
 .facebook i{
     left:11px;
}
 .facebook{
     background-color: #3b5998;
     margin: 0px 5px;
}
 .twitter{
     background-color: #1dcaff;
     margin: 0px 5px;
}
 .whatsapp{
     background-color: #34af23;
     margin: 0px 5px;
}
 .whatsapp i{
     top:8px;
}

 .movie-card-box:hover .movie-overlay .movie-card-wrapper .bottom-section{
  bottom: 0px;
}
 .movie-card-box:hover .movie-overlay .movie-card-wrapper .social-icons{
     opacity:1;
     visibility: visible;
}
 .movie-card-box:hover .movie-overlay .movie-card-wrapper .social-icons .icons{
     left: 0px;
     -webkit-transform: rotate(-360deg);
     -ms-transform: rotate(-360deg) ;
         transform: rotate(-360deg) ;
}
 
  .error-container{
     position: relative;
     top: 85px;
     width: 100%;
     height: 90vh;
     margin: 0 auto;
     background: #1c1c2d;
}
 .error{
     display: block;
     width: 100%;
     position: relative;
     top: 15%;
     padding: 50px 0;
     overflow: hidden;
     font-style: italic;
     text-align: center;
     vertical-align: middle;
     font-size: 150px;
     text-decoration: underline;
     color: #fff;
 }

  .no-movies-container{
     position: relative;
     top: 85px;
     width: 100%;
     margin: 0 auto;
     background: #fff;
}
 .no-movies{
     display: block;
     width: 100%;
     position: relative;
     top: 0px;
     padding: 20px 0;
     overflow: hidden;
     font-style: italic;
     text-align: center;
     vertical-align: middle;
     font-size: 5em;
     text-decoration: none;
     color: #1c1c2d;
 }

 .fa-heart{
     color: red;
 }
.fav_movies_text{
     color: #1c1c2d;
      font-size: 3em;
      text-align: center;
      padding: 40px 10px;
      font-weight: bold;
      font-style: italic;
}
  .fav-movies-container{
     position: relative;
     top: 85px;
     width: 100%;
     margin: 0 auto;
     background: #fff;
}
.fav-movie-card-container{
     display: block;
     position: relative;
     top: 0px;
     max-width: 1044px;
     width: 100%;
     margin: 0 auto;
}


